import { Link, LoaderFunction, useLoaderData } from "remix";
import { getNotes, getSite } from "~/lib/api";

export const loader: LoaderFunction = async () => {
  const envSitePath = process.env.SITE_PATH as string;
  const site = await getSite({ path: envSitePath });
  const notes = await getNotes({ site_path: envSitePath });
  return { site: site, notes: notes };
};

export default function Index() {
  const { site, notes } = useLoaderData();

  return (
    <div className="bg-white">
      <div className="max-w-7xl mx-auto py-16 px-4 sm:py-24 sm:px-6 lg:px-8">
        <div className="text-center">
          <h2 className="text-base font-semibold text-indigo-600 tracking-wide uppercase">
            Newsletter
          </h2>
          <p className="mt-1 text-4xl font-extrabold text-gray-900 sm:text-5xl sm:tracking-tight lg:text-6xl">
            {site.site.name}
          </p>
          <p className="max-w-xl mt-5 mx-auto text-xl text-gray-500">
            {site.site.headline}
          </p>
          <p className="pt-5">
            <Link
              to={"/posts"}
              className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-full"
            >
              Archive
            </Link>
          </p>
        </div>
        <div>
          <ul role="list" className="divide-y divide-gray-200">
            {site.notes.map((note) => (
              <li
                key={note.id}
                className="relative bg-white py-5 px-4 hover:bg-gray-50 focus-within:ring-2 focus-within:ring-inset focus-within:ring-indigo-600"
              >
                <div className="flex justify-between space-x-3">
                  <div className="min-w-0 flex-1">
                    <a href="#" className="block focus:outline-none">
                      <span className="absolute inset-0" aria-hidden="true" />
                      <p className="text-2xl font-bold text-gray-900 truncate">
                        {note.title}
                      </p>
                    </a>
                  </div>
                  <time
                    dateTime={note.created_at}
                    className="flex-shrink-0 whitespace-nowrap text-sm text-gray-500"
                  >
                    {note.created_at}
                  </time>
                </div>
              </li>
            ))}
          </ul>
        </div>
      </div>
    </div>
  );
}
